video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.hero-container>h1 {
    color: white;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container>p {
    margin-top: 8px;
    color: white;
    font-size: 32px;
    font-family: 'Trebuchet MS', sans-serif;
}

.hero-btns {
    margin-top: 32px;
}

.hero-btns .btn {
    margin: 6px;
}

.fa-play-cirlce {
    margin-left: 4px;
}

@media screen and (max-width: 1200px) {
    .hero-container>h1 {
        font-size: 82px;
    }
    .hero-container>p {
        font-size: 30px;
    }
}

@media screen and (max-width: 960px) {
    .hero-container>h1 {
        font-size: 64px;
        margin-top: -150px;
    }
    .hero-container>p {
        font-size: 28px;
    }
}

@media screen and (max-width: 768px) {
    .hero-container>h1 {
        font-size: 50px;
        margin-top: -100px;
    }
    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 100%;
    }
}

@media screen and (max-width: 560px) {
    video {
        display: none;
        height: 1px;
        width: 1px;
    }
    .hero-container {
        background: url('../images/Sky.jpg') center center/cover no-repeat;
    }
    .hero-container>h1 {
        font-size: 40px;
        margin-top: -150px;
    }
    .hero-container>p {
        font-size: 24px;
    }
}

@media screen and (max-width: 560px) {
    .hero-container>h1 {
        font-size: 36px;
    }
    .hero-container>p {
        font-size: 22px;
    }
}

@media screen and (max-width: 410px) {
    .hero-container>h1 {
        font-size: 34px;
    }
    .hero-container>p {
        font-size: 20px;
    }
}